.btn-inline{
    border: none;
    color: $color-primary;
    font-size: inherit;
    border-bottom: 1px solid currentColor;
    padding-bottom: 2px;
    display: inline-block;
    background-color: transparent;
    cursor: pointer;
    transition: all .2s;

    & span{
        margin-left: 3px;
        transition: margin .2s;
    }

    &:hover{
        color: $color-grey-dark-1;

        span{
            margin-left: 8px;
        }
    }

    &:focus{
        outline: none;
        animation: pulsate 1s infinite;
    }
}


@keyframes pulsate{

    0%{
        transform: scale(1);
        box-shadow: none;
    }

    50%{
        transform: scale(1.05);
        box-shadow: 0 1rem 4rem rgba(black,.25);
    }

    100%{
        transform: scale(1);
        box-shadow: none;
    }
}