
.list{
    list-style: none;
    margin: 3rem 0;
    padding: 3rem 0;
    border-top: $line;
    border-bottom: $line;
    display: flex;
    flex-wrap: wrap;
    
    &__item{
        flex: 0 0 50%;
        margin-bottom: .7rem;
    }

    &__item::before{
        content: "";
        display: inline-block;
        height: 1rem;
        width: 1rem;
        margin-right: .7rem;
        background-color: $color-primary;
        mask-image: url("./img/chevron-thin-right.svg");
        mask-size: cover;
        -webkit-mask-image: url("./img/chevron-thin-right.svg");
        -webkit-mask-size: cover;
    }
}