.overview{

    display: flex;
    align-items: center;
    border-bottom: $line;

    &__heading{
        font-size: 2.25rem;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 1px;
        padding: 1.5rem;
    }
    &__stars{
        margin-right: auto;
        display: flex;
    }
    &__icon-star,
    &__icon-location{
        width: 1.75rem;
        height: 1.75rem;
        fill: $color-primary;
    }
    &__location{
        font-size: 1.2rem;
        display: flex;
        vertical-align: center;
    }

    &__icon-location{
        margin-right: .5rem;
    }

    &__rating{
        background-color: $color-primary;
        color: #fff;
        margin-left: 3rem;
        align-self: stretch;
        padding: 0 2.25rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__rating-average{
        font-size: 2.25rem;
        font-weight: 300;
    
    }
    &__rating-count{
        font-size: .8rem;
        text-transform: uppercase;
    }
}