.recommend{
    font-size: 1.3rem;
    color: $color-grey-dark-3;
    display: flex;
    align-items: center;


    &__count{
        margin-right: auto;
    }

    &__photo{

        box-sizing: content-box;
        height: 4rem;
        width: 4rem;
        border-radius: 50%;
        border: 3px solid white;
        &:not(:last-child){
            margin-right: -1.5rem;
        }
    }
}