//main-component user navbar
.user-nav{
    display: flex;
    align-items: center;
    align-self: stretch;

    //all direct child access
    & > * {
        padding: 0 2rem;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;
    }
    & > *:hover{
       background-color: $color-grey-light-2;
    }
    &__icon-box{
        position: relative;
    }
    &__icon{
        height: 2.25rem;
        width: 2.25rem;
        fill: $color-grey-dark-2;
  
    }
    &__notification{
        font-size: .8rem;
        height: 1.75rem;
        width: 1.75rem;
        border-radius: 50%;
        background-color: $color-primary;
        color: #fff;
        position: absolute;
        top: 1.5rem;
        right: 1.1rem;

        display: flex;
        justify-content: center;
        align-items: center;

    }
    &__user{
     
    }
    &__user-photo{
        height: 3.75rem;
        border-radius: 50%;
        margin-right: 1rem;
    }
    &__user-name{

    }
}